.buttons {
    background: teal;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 1px 1px 3px black;
    width: fit-content;
    color: white;
}

.questionTitle {
    margin: auto;
    text-align: center;
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--dark-primary-color);
}

.namesSelectSeries {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 90px);
    gap: 20px;
    margin: 50px auto;
    max-width: 500px;
}

.nameSelect {
    box-shadow: 1px 1px 3px #0000004d;
    border-radius: 5px;
    background: wheat;
    -webkit-user-select: none;
    user-select: none;
    display: grid;
    grid-template-rows: auto auto;
    /* grid-template-columns: 200px auto; */
    align-items: center;
    text-align: center;
    font-size: 1.4rem;
    color: #000000ba;
    opacity: 0.5;
}

.new {
    background: rgb(246, 212, 147);
    opacity: 1;
    cursor: pointer;
    position: relative;
}

.newSeries {
    animation: enter;
    animation-duration: 300ms;
}

@keyframes enter {
    from {
        transform: translateY(-50%) scaleY(0);
    }
    to {
        top: 0px;
        transform: translateY(0%) scaleY(1);
    }
}